@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;600;700&family=Inter:wght@300;400;500;600;700&family=Roboto+Mono:wght@400;500&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    :root {
        /* Dark Luxury Casino Theme - Gold/Black/Purple */
        --background: 240 10% 4%;
        --foreground: 45 100% 95%;
        
        --card: 240 10% 8%;
        --card-foreground: 45 100% 95%;
        
        --popover: 240 10% 6%;
        --popover-foreground: 45 100% 95%;
        
        /* Gold Primary */
        --primary: 43 74% 49%;
        --primary-foreground: 240 10% 4%;
        
        /* Deep Purple Secondary */
        --secondary: 270 60% 30%;
        --secondary-foreground: 45 100% 95%;
        
        /* Muted Dark */
        --muted: 240 10% 15%;
        --muted-foreground: 240 5% 65%;
        
        /* Purple Accent */
        --accent: 280 60% 45%;
        --accent-foreground: 45 100% 95%;
        
        --destructive: 0 72% 51%;
        --destructive-foreground: 0 0% 98%;
        
        /* Success Green */
        --success: 142 76% 36%;
        --success-foreground: 0 0% 98%;
        
        /* Warning Orange */
        --warning: 38 92% 50%;
        --warning-foreground: 0 0% 10%;
        
        --border: 240 10% 20%;
        --input: 240 10% 15%;
        --ring: 43 74% 49%;
        
        /* Rarity Colors */
        --rarity-common: 220 10% 50%;
        --rarity-uncommon: 142 70% 45%;
        --rarity-rare: 217 91% 60%;
        --rarity-epic: 280 70% 60%;
        --rarity-mythic: 43 100% 60%;
        
        /* Custom Game Colors */
        --gold: 43 74% 49%;
        --gold-light: 45 90% 65%;
        --gold-dark: 40 70% 35%;
        --purple: 270 60% 45%;
        --purple-light: 280 70% 60%;
        --purple-dark: 260 50% 30%;
        
        /* Chart Colors */
        --chart-1: 43 74% 49%;
        --chart-2: 142 70% 45%;
        --chart-3: 217 91% 60%;
        --chart-4: 280 70% 60%;
        --chart-5: 0 72% 51%;
        
        --radius: 0.75rem;
    }
}

@layer base {
    * {
        @apply border-border;
    }
    
    body {
        @apply bg-background text-foreground antialiased;
        font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
    }
    
    h1, h2, h3, h4, h5, h6 {
        font-family: 'Playfair Display', Georgia, serif;
    }
    
    .font-display {
        font-family: 'Playfair Display', Georgia, serif;
    }
    
    .font-mono {
        font-family: 'Roboto Mono', monospace;
    }
}

/* Custom Scrollbar */
::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

::-webkit-scrollbar-track {
    background: hsl(var(--background));
}

::-webkit-scrollbar-thumb {
    background: hsl(var(--gold) / 0.3);
    border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
    background: hsl(var(--gold) / 0.5);
}

/* Rarity Border Classes */
.rarity-common {
    border-color: hsl(var(--rarity-common));
    box-shadow: 0 0 10px hsl(var(--rarity-common) / 0.3);
}

.rarity-uncommon {
    border-color: hsl(var(--rarity-uncommon));
    box-shadow: 0 0 15px hsl(var(--rarity-uncommon) / 0.4);
}

.rarity-rare {
    border-color: hsl(var(--rarity-rare));
    box-shadow: 0 0 20px hsl(var(--rarity-rare) / 0.5);
}

.rarity-epic {
    border-color: hsl(var(--rarity-epic));
    box-shadow: 0 0 25px hsl(var(--rarity-epic) / 0.5);
}

.rarity-mythic {
    border-color: hsl(var(--rarity-mythic));
    box-shadow: 0 0 30px hsl(var(--rarity-mythic) / 0.6);
    animation: mythic-glow 2s ease-in-out infinite alternate;
}

@keyframes mythic-glow {
    from {
        box-shadow: 0 0 20px hsl(var(--rarity-mythic) / 0.4), 0 0 40px hsl(var(--rarity-mythic) / 0.2);
    }
    to {
        box-shadow: 0 0 30px hsl(var(--rarity-mythic) / 0.6), 0 0 60px hsl(var(--rarity-mythic) / 0.3);
    }
}

/* Gold Shimmer Effect */
.gold-shimmer {
    background: linear-gradient(
        110deg,
        hsl(var(--gold-dark)) 0%,
        hsl(var(--gold)) 45%,
        hsl(var(--gold-light)) 50%,
        hsl(var(--gold)) 55%,
        hsl(var(--gold-dark)) 100%
    );
    background-size: 200% 100%;
    animation: shimmer 3s ease-in-out infinite;
}

@keyframes shimmer {
    0%, 100% { background-position: 200% 0; }
    50% { background-position: -200% 0; }
}

/* Pulse Animation for Click Button */
.click-pulse {
    animation: click-pulse 0.15s ease-out;
}

@keyframes click-pulse {
    0% { transform: scale(1); }
    50% { transform: scale(0.95); }
    100% { transform: scale(1); }
}

/* Coin Float Animation */
.coin-float {
    animation: coin-float 1s ease-out forwards;
}

@keyframes coin-float {
    0% {
        opacity: 1;
        transform: translateY(0) scale(1);
    }
    100% {
        opacity: 0;
        transform: translateY(-60px) scale(0.5);
    }
}

/* Card Hover Effect */
.card-hover {
    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1), 
                box-shadow 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.card-hover:hover {
    transform: translateY(-4px);
    box-shadow: 0 20px 40px hsl(var(--gold) / 0.15);
}

/* Progress Bar Glow */
.progress-glow {
    box-shadow: 0 0 10px hsl(var(--gold) / 0.5);
}

/* Stock Ticker Animation */
.stock-up {
    color: hsl(var(--success));
    animation: stock-pulse 0.5s ease-out;
}

.stock-down {
    color: hsl(var(--destructive));
    animation: stock-pulse 0.5s ease-out;
}

@keyframes stock-pulse {
    0%, 100% { opacity: 1; }
    50% { opacity: 0.5; }
}

/* Glass Effect */
.glass-effect {
    background: hsl(var(--card) / 0.8);
    backdrop-filter: blur(12px);
    border: 1px solid hsl(var(--gold) / 0.2);
}

/* Gradient Text */
.gradient-text-gold {
    background: linear-gradient(135deg, hsl(var(--gold-light)), hsl(var(--gold)), hsl(var(--gold-dark)));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

/* Noise Background Texture */
.noise-bg {
    position: relative;
}

.noise-bg::before {
    content: '';
    position: absolute;
    inset: 0;
    background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 256 256' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noise'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.8' numOctaves='4' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noise)'/%3E%3C/svg%3E");
    opacity: 0.03;
    pointer-events: none;
}

/* Achievement Badge Glow */
.achievement-glow {
    animation: achievement-glow 2s ease-in-out infinite;
}

@keyframes achievement-glow {
    0%, 100% { filter: drop-shadow(0 0 5px hsl(var(--gold) / 0.5)); }
    50% { filter: drop-shadow(0 0 15px hsl(var(--gold) / 0.8)); }
}

/* Countdown Timer Animation */
.countdown-urgent {
    animation: countdown-flash 0.5s ease-in-out infinite;
}

@keyframes countdown-flash {
    0%, 100% { color: hsl(var(--destructive)); }
    50% { color: hsl(var(--warning)); }
}

/* Level Up Animation */
.level-up {
    animation: level-up 0.6s cubic-bezier(0.4, 0, 0.2, 1);
}

@keyframes level-up {
    0% { transform: scale(1); }
    25% { transform: scale(1.1); }
    50% { transform: scale(0.95); }
    100% { transform: scale(1); }
}

/* Tab Indicator */
.tab-indicator {
    position: relative;
}

.tab-indicator::after {
    content: '';
    position: absolute;
    bottom: -2px;
    left: 0;
    right: 0;
    height: 2px;
    background: linear-gradient(90deg, transparent, hsl(var(--gold)), transparent);
}

/* Custom Button Styles */
.btn-gold {
    background: linear-gradient(135deg, hsl(var(--gold-dark)), hsl(var(--gold)), hsl(var(--gold-dark)));
    color: hsl(var(--background));
    font-weight: 600;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.btn-gold:hover {
    box-shadow: 0 0 30px hsl(var(--gold) / 0.5);
    transform: translateY(-2px);
}

.btn-purple {
    background: linear-gradient(135deg, hsl(var(--purple-dark)), hsl(var(--purple)), hsl(var(--purple-dark)));
    color: hsl(var(--foreground));
    font-weight: 600;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.btn-purple:hover {
    box-shadow: 0 0 30px hsl(var(--purple) / 0.5);
    transform: translateY(-2px);
}

/* Event Banner */
.event-banner {
    background: linear-gradient(90deg, 
        hsl(var(--purple-dark) / 0.8), 
        hsl(var(--purple) / 0.6), 
        hsl(var(--purple-dark) / 0.8)
    );
    border: 1px solid hsl(var(--gold) / 0.3);
}

/* Number Counter Animation */
.number-increase {
    animation: number-pop 0.3s ease-out;
}

@keyframes number-pop {
    0% { transform: scale(1); }
    50% { transform: scale(1.2); color: hsl(var(--success)); }
    100% { transform: scale(1); }
}

/* Premium Badge */
.premium-badge {
    background: linear-gradient(135deg, hsl(var(--gold)), hsl(45 100% 70%), hsl(var(--gold)));
    color: hsl(var(--background));
    animation: premium-shine 3s ease-in-out infinite;
}

@keyframes premium-shine {
    0%, 100% { filter: brightness(1); }
    50% { filter: brightness(1.2); }
}

/* Debug wrapper fix */
@layer base {
    [data-debug-wrapper="true"] {
        display: contents !important;
    }
}
